import  Vue                 from 'vue'
import  Vuex                from 'vuex'
import  * as getters        from './getters'
import  * as actions        from './actions'
import  * as mutations      from './mutations'
// "" - "" 

Vue.use(Vuex)
// 
const state = {
    city: {                                             // 
        cityCode: '3702'
    }
    ,loadingState: {
        judgeShow: true
    }                                                   // 
    ,bannerImg_Arr: []                                  // "" - banner
    ,hotBuildingList_Arr: []                            // "" - ""
    ,furnitureInfo: {
        appPic: '',
        code: '',
        description: '',
        furnitureSizeList: [],
        imgUrl: [],
        name: '',
        price: 0,
        type: '',
        judgeShow: true
    }
    ,researchInfo: {                                    // "" -
        IPList: [],
        address: '',
        appPic: '',
        description: '',
        name: '',
        judgeShow: true
    }
    ,officeBuilding: {                                  // "" -
        judgeShow: true,
        buildingRelationList_judgeShow: true
    }
    ,coWorking: {                                       // "" - 
        judgeShow: true,
        buildingRelationList_judgeShow: true,
        nameInfo: '0'
    }
    ,regionPointList: []                                        // '' (  )
    ,BCPointList: []                                            // '' 
    ,buildingPointList: []                                      // '' 
    ,mapView: {
         typeRegionPointList: []                                // (  )
    }
    // (  /   )
    ,searchValue: {
        // 
        cityCode:               ''                                      // code
        ,indexLevel:            'administrative'                        // : administrative; : business; : building
        ,regionCode:            ''                                      // 
        ,businessCircleCode:    ''                                      // 
        // 
        ,btype:                 'A'                                     // ( A / B  )
        ,priceDayMin:           '0'                                     // (  )
        ,priceDayMax:           ''                                      // (  )
        ,priceMonthMin:         '0'                                     // (  )
        ,priceMonthMax:         ''                                      // (  )
        ,decoration:            ''                                      // ( A:; B:; C:; D: )
        // 
        ,administrative_Arr:    ''                                      // 
        ,business_Arr:          ''                                      // 
        ,building_Arr:          ''                                      // 
    }
    ,searchMapRequest_Arr: []                                           // ,( watch )
}
const store = new Vuex.Store({
    state,
    getters,
    actions,
    mutations,
})
if (module.hot) {
    module.hot.accept([
        './getters',
        './actions',
        './mutations',
    ], () => {
        store.hotUpdate({
            getters: require('./getters'),
            actions: require('./actions'),
            mutations: require('./mutations'),
        })
    })
}

export default store



